import { Link } from "react-router-dom";
import "../Homepage/source/style.css"

function CartProduct(){
    return (
        <>
        <div className="CartProduct">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                    <table className="table  text-center ">
  <thead>
    <tr>
      {/* <th scope="col">Remove</th> */}
      <th scope="col" colSpan="3" >Product</th>
      <th scope="col">Price</th>
      <th scope="col">Quantity</th>
      <th scope="col">Subtotal</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      {/* <th scope="row"><button type="button" class="btn btn-danger">X</button></th> */}
      <td colSpan="3"><button type="button" className="btn btn-danger">X</button> DNK Yellow Shoes</td>
      <td className="salery">150</td>
      <td>2</td>
      <td className="salery">300</td>
    </tr>

  </tbody>
</table>
 </div>
   <div className="col-md-6 coupon">
    <div className="row">
  <div className="col">
    <input type="text" className="form-control" placeholder="Coupon code" aria-label="Coupon code"/>
  </div>
  <div className="col">
  <button type="button" className="btn btn-info">Submit</button>
  </div>
</div>
 </div>
<div className="col-md-6">
 <table className="table table-bordered tab2">
  <thead>
    <tr>
      
      <th scope="col">Cart total</th>
      <th scope="col">Salery</th>
      
    </tr>
  </thead>
  <tbody>
    <tr>
      <th scope="row">Subtotal</th>
      <td> 150 EGP </td>
    </tr>
    <tr>
      <th scope="row">Shipping</th>
      <td>Free </td>
    </tr>
    <tr>
      <th scope="row">Total</th>
      <td>150 EGP</td>
    </tr>
    <tr>
      <th scope="row" colSpan="2">  <Link  to={"/Checkout"} type="button" className="btn btn-info w-100">Checkout</Link></th>
   
    </tr>


  </tbody>
</table>
                    </div>
                </div>
            </div>
        </div>
        
        </>
    )
}
export default CartProduct;