// import axios from 'axios'
// import { data } from 'isotope-layout'
// import React, { useState } from 'react'

function Modal(){
  // const [username,setUsername]=useState("")
  // const [password,setPassword]=useState(+"")
// const forSubmit=()=>{
// axios.post("http://localhost:9000/products",{
//   username,password
// }).then((data)=>{
//   console.log(data)
// })

// }
    return(
     
        <>
        
                    {/* <!-- Modal --> */}
                    <div
              className="modal fade"
              id="login"
              tabIndex="-1"
              aria-labelledby="login"
              aria-hidden="true"
            >
              <div className="modal-dialog modal-dialog modal-dialog-centered">
                <div className="modal-content ">
                  <div className="modal-header">
                    <h1 className="modal-title fs-5 " id="login">
                      Login
                    </h1>
                  </div>
                  <div className="modal-body">
                    <div className="form-floating mb-3">
                      <input 
                        type="e-mail"
                        className="form-control"
                        id="floatingInput"
                        placeholder="name@example.com"
                        // onChange={(e)=>setUsername(e.target.value)}
                      />
                      <label htmlFor="floatingInput">Email address</label>
                    </div>
                    <div className="form-floating">
                      <input
                        type="password"
                        className="form-control"
                        id="floatingPassword"
                        placeholder="Password"
                        // onChange={(e)=>setPassword(e.target.value)}
                      />
                      <label htmlFor="floatingPassword">Password</label>
                    </div>
                  </div>
                  <div className="modal-footer">
                    <div className="remeber">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id="gridCheck"
                      />
                      <label className="form-check-label" htmlFor="gridCheck">
                        Remember me
                      </label>
                    </div>
                    <button type="submit" className="btn btn-success">
                      Login
                    </button>
                  </div>

                  <div className="forget">
                    <h5
                      className="forgetpassword"
                      data-bs-toggle="modal"
                      data-bs-target="#forget"
                    >
                      Forget password?
                    </h5>
                    <div className="dontHave">
                      <h5 className="lead">Dont have account?</h5>
                      <h5
                        className="sign"
                        data-bs-toggle="modal"
                        data-bs-target="#signup"
                      >
                        Sign up
                      </h5>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/*sign up modal*/}

            <div
              className="modal fade model-two model-signup"
              id="signup"
              tabIndex="-1"
              aria-labelledby="exampleModalLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog">
                <div className="modal-content">
                  <div className="modal-header">
                    <h1 className="modal-title fs-5" id="exampleModalLabel">
                      Sign up
                    </h1>
                  </div>
                  <div className="modal-body">
                    <div className="form-floating mb-3">
                      <input
                        type="text"
                        className="form-control"
                        id="signup name"
                        placeholder="Nme"
                      />
                      <label htmlFor="signup name">Name</label>
                    </div>
                    <div className="form-floating mb-3">
                      <input
                        type="email"
                        className="form-control"
                        id="signup Input"
                        placeholder="name@example.com"
                      />
                      <label htmlFor="signup Input">Email address</label>
                    </div>
                    <div className="form-floating mb-3">
                      <input
                        type="password"
                        className="form-control"
                        id="signupassword"
                        placeholder="New password"
                      />
                      <label htmlFor="signupPassword"> password</label>
                    </div>
                    <div className="form-floating">
                      <input
                        type="password"
                        className="form-control"
                        id="signup Confirm Password"
                        placeholder="Confirm new password"
                      />
                      <label htmlFor="signup Confirm Password">
                        Confirm new password
                      </label>
                    </div>
                  </div>

                  <div className="modal-footer">
                    <div className="Have-acount ">
                      <h5 className="lead">Have an account?</h5>
                      <h5
                        className="log"
                        data-bs-toggle="modal"
                        data-bs-target="#login"
                      >
                        Login
                      </h5>
                    </div>
                    <button type="button" className="btn btn-success">
                      Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>

            {/*sign up modal*/}
            {/* <!-- Modal forgetpass --> */}

            <div
              className="modal fade model-two"
              id="forget"
              tabIndex="-1"
              aria-labelledby="exampleModalLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog">
                <div className="modal-content">
                  <div className="modal-header">
                    <h1 className="modal-title fs-5" id="exampleModalLabel">
                      Forget password
                    </h1>
                  </div>
                  <div className="modal-body">
                    <p className="lead">
                      Enter your email, we will send you code to reset your
                      password
                    </p>
                    <div className="form-floating mb-3">
                      <input
                        type="email"
                        className="form-control"
                        id="Input"
                        placeholder="name@example.com"
                      />
                      <label htmlFor="Input">Email address</label>
                    </div>
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-success"
                      data-bs-toggle="modal"
                      data-bs-target="#submit"
                    >
                      Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>

            {/* <!-- Modalforgetpass --> */}
            {/* <!-- Modalsubmit --> */}
            <div
              className="modal fade model-two model-three"
              id="submit"
              tabIndex="-1"
              aria-labelledby="exampleModalLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog">
                <div className="modal-content">
                  <div className="modal-header">
                    <h1 className="modal-title fs-5" id="exampleModalLabel">
                      Verification
                    </h1>
                  </div>
                  <div className="modal-body">
                    <p className="lead">
                      Enter the 4 digits code that send to your email address.
                    </p>
                    <div className="verification-num">
                      <ul className="verification-list">
                        <li className="num">
                          <input type="text" placeholder="#" />
                        </li>
                        <li className="num">
                          <input type="text" placeholder="#" />
                        </li>
                        <li className="num">
                          <input type="text" placeholder="#" />
                        </li>
                        <li className="num">
                          <input type="text" placeholder="#" />
                        </li>
                      </ul>
                    </div>
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-success"
                      data-bs-toggle="modal"
                      data-bs-target="#resitpassword"
                    >
                      Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>

            {/* <!-- Modalsubmit --> */}
            {/* <!-- Modalresitpasword --> */}
            <div
              className="modal fade model-two model-four"
              id="resitpassword"
              tabIndex="-1"
              aria-labelledby="exampleModalLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog">
                <div className="modal-content">
                  <div className="modal-header">
                    <h1 className="modal-title fs-5" id="exampleModalLabel">
                      Reset password
                    </h1>
                  </div>
                  <div className="modal-body">
                    <div className="form-floating mb-3">
                      <input
                        type="password"
                        className="form-control"
                        id="newPassword"
                        placeholder="New password"
                      />
                      <label htmlFor="newPassword">New password</label>
                    </div>
                    <div className="form-floating">
                      <input
                        type="password"
                        className="form-control"
                        id="Confirm Password"
                        placeholder="Confirm new password"
                      />
                      <label htmlFor="Confirm Password">
                        Confirm new password
                      </label>
                    </div>
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-success"
                      data-bs-toggle="modal"
                      data-bs-target="#login"
                    >
                      Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>

            {/* <!-- Modalresitpasword --> */}

        </>
    )
}
 
export default Modal;