import { useEffect, useState } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faStar} from "@fortawesome/free-regular-svg-icons"
import { Link } from "react-router-dom";



function Feature(props){
    const [product,setProduct]=useState([])
    useEffect(()=>{
        fetch('https://fakestoreapi.com/products')
            .then(res=>res.json())
            .then((date)=>setProduct(date))
    },[])
    
    return (
            <div className="feature">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                  <div className="text text-center" >
                    <h1 className="master ">{props.kind}</h1>
                  </div>
                  </div>
                  
                    {product?.map((e)=>{
                        
                        return (
                           
                            <div className="col-md-4  col-sm-6 mb-4"key={e.id}>
                                <div className="card w-100 h-100 border-0" >
                            <div className="img"> 
                                <div className="backgroundImageCard" style={{ backgroundImage: 'url(' + e.image + ')' }}></div>
                          
                            </div>
                        <div className="card-body w-100">
                          <h5 className="card-title">{e.title} ....</h5>
                                <div className="quality d-flex justify-content-between">
                                    <p className="kind">Men</p>
                                    <ul className="stars d-flex">
                                        <li className="star"><FontAwesomeIcon icon={faStar}  className="start"/></li>
                                        <li className="star"><FontAwesomeIcon icon={faStar} className="start"/></li>
                                        <li className="star"><FontAwesomeIcon icon={faStar}className="start" /></li>
                                        <li className="star"><FontAwesomeIcon icon={faStar}className="start" /></li>
                                        <li className="star"><FontAwesomeIcon icon={faStar} className="start"/></li>
                                    </ul>
                                
                                </div>
                                <div className="Salery d-flex justify-content-between">
                                        <p className="sale lead">150</p>
                                        <h1 className="sale">EGP {e.price}</h1>
                                </div>


                                <div className="detils d-flex justify-content-between">
                                <Link to={`/Woman/${e.id}`} type="button" className="btn view btn-info btn-sm button">View</Link>
                                <Link to={"/Cart"} type="button" className="btn btn-success">Add Cart</Link>



 
  </div>
                            <div className="color">
                                <ul className="colorList">
                                    {e.color?.map((p,index)=>{
                                        return <li className="choseColor" style={{backgroundColor:`${p}`}} key={index}></li>
                                            
                                         })}
                                </ul>
                            </div>
  
  
                        </div>
                        </div>
                       </div>
                       
                       )
                    })}
                  </div>
                </div>
            </div>

    )
}
export default Feature;