
//theme
import "primereact/resources/themes/lara-light-indigo/theme.css";     
    
//core
import "primereact/resources/primereact.min.css";

//icons
import "primeicons/primeicons.css";   

import { Message } from 'primereact/message';
import "../../Homepage/source/style.css"

import React from 'react'; 
import { Button } from 'primereact/button';
import { useCounter } from 'primereact/hooks';


export default function BasicDemo() {
    const { count, increment, decrement } = useCounter(1);

if (count<1) {
    return(
        <>

        
<div className="card flex flex-column align-items-center counter">
            <div className="flex flex-wrap gap-3 count">
            <Button icon="pi pi-minus" className="p-button-outlined " style={{opacity:"0.2"}} ></Button>
                <span className="font-bold text-4xl ">{count}</span>

                <Button icon="pi pi-plus" className="p-button-outlined  p-button-success" onClick={increment}></Button>
            </div>
        </div>
       
         
        <Message severity="error" text="The Count Cant't be less than one" className="massage" />
    </>
    )
} else {
    return (
        <div className="card flex flex-column align-items-center counter">
            <div className="flex flex-wrap gap-3 count">
            <Button icon="pi pi-minus" className="p-button-outlined " onClick={decrement}></Button>
                <span className="font-bold text-4xl ">{count}</span>

                <Button icon="pi pi-plus" className="p-button-outlined  p-button-success" onClick={increment}></Button>
            </div>
        </div>
        
    )
}

    // return (
    //     <div className="card flex flex-column align-items-center counter">
    //         <div className="flex flex-wrap gap-3 count">
    //         <Button icon="pi pi-minus" className="p-button-outlined " onClick={decrement}></Button>
    //             <span className="font-bold text-4xl ">{count}</span>

    //             <Button icon="pi pi-plus" className="p-button-outlined  p-button-success" onClick={increment}></Button>
    //         </div>
    //     </div>
        
    // )
    
}