import "../Homepage/source/style.css"
//theme
import "primereact/resources/themes/lara-light-indigo/theme.css";     
    
//core
import "primereact/resources/primereact.min.css";

//icons
import React, { useState } from "react";
import { Dropdown } from 'primereact/dropdown';

export default function ClearIconDemo() {
    const [selectedCity, setSelectedCity] = useState(null);
    const cities = [
        { name: 'sort by popurlity', code: 'PO' },
        { name: 'sort by average', code: 'AV' },
        { name: 'sort by latest', code: 'LS' },
      
       
    ];

    return (
        <div className=" button-select">
            <Dropdown value={selectedCity} onChange={(e) => setSelectedCity(e.value)} options={cities} optionLabel="name" 
                showClear placeholder="Default sorting" className="drop"/>
        </div>
    )
}