import { MyComponent } from "../../../Homepage/Rating";
import "../../../Homepage/source/style.css"
function SendEmail(){
    return (
        <>
        <div className="SendEmail">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                    <form className="row g-3 forme">
                        <div className="col-md-12">Your email address will not be published. Required fields are marked *</div>
                        <div className="col-md-6">
    
    <input type="text" className="form-control" id="inputPassword4" placeholder="Your Name"/>
  </div>
  <div className="col-md-6">
  
    <input type="email" className="form-control" id="inputEmail4" placeholder="Your E-mail"/>
  </div>

  <div className="col-md-12">
  <textarea className="form-control textArea" placeholder="Your review" id="floatingTextarea2" style={{height: "100px"}}></textarea>

  </div>
  <div className="col-md-12">
  <div className="form-check">
  <input className="form-check-input" type="checkbox"  id="flexCheckIndeterminate"/>
  <label className="form-check-label" htmlFor="flexCheckIndeterminate">
  Save my name, email, and website in this browser for the next time I comment.
  </label>
</div>
<MyComponent/>
  </div>
  <div className="col-12 send-but">
    <button type="submit" className="btn btn-success ">Submit</button>
  </div>
</form>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}
export default SendEmail;