import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser, faBagShopping } from "@fortawesome/free-solid-svg-icons";
import "../compunt/Homepage/source/style.css";
import { Link } from "react-router-dom";
import Modal from "./Modal";
import CartSideBar from "./CartSideBar";
function NavBar() {
  return (
    <>
    <nav className="navbar navbar-expand-lg ">
      <div className="container">
        <Link className="navbar-brand" to="/">
          Logo
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto ms-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to="/">
                Everything
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link active" to="/woman">
                Women
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link active" to="/Man">
                Man
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link active" to="/Acecory">
                Accessories
              </Link>
            </li>
          </ul>
          <div className="d-flex  forme ">
            <div className="salary align-self-center me-2" data-bs-toggle="offcanvas" data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">
              {" "}
              EGP 0 <FontAwesomeIcon icon={faBagShopping} />
            </div>
            <span className="align-self-center"> | </span>
            <button
              type="button"
              className="btn btn-success "
              data-bs-toggle="modal"
              data-bs-target="#login"
            >
              {" "}
              <FontAwesomeIcon icon={faUser} /> Login
            </button>
            <Modal />
          </div>

          <form>
            <div className="btn-group">
              <button
                type="button"
                className="btn nav-btn btn-Light dropdown-toggle"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                EN
              </button>
              <ul className="dropdown-menu">
                <li>
                  <a className="dropdown-item" href="/#">
                    AR
                  </a>
                </li>
              </ul>
            </div>
          </form>
        </div>
      </div>
    </nav>
<CartSideBar/>
    </>
  );
}

export default NavBar;
