
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faStar} from "@fortawesome/free-regular-svg-icons"
import { Link} from "react-router-dom";
import "../../../Homepage/source/style.css"
import { useEffect, useState } from "react"




function SomeProduct(){

 

    const[product,setProduct]=useState([])
    useEffect(()=>{fetch("https://fakestoreapi.com/products")
    .then((res)=>res.json())
    .then((data)=>setProduct(data))},[])


    return(<>
    <div className='someProduct'>
        <div className='container'>
            <div className='row'>
                
    { product?.slice(0,4).map((e)=>{
       
    return (
                        
        <div className="col-md-6 col-sm-6 col-lg-3 mt-5 mb-4"key={e.id}>
            <div className="card border-0 w-100 h-100" >
        <div className="img d-flex justify-content-center"> 
            <div className="backgroundImageCard" style={{ backgroundImage: 'url(' + e.image + ')' }}></div>
      
        </div>
    <div className="card-body w-100">
      <h5 className="card-title">{e.title} ....</h5>
            <div className="quality d-flex justify-content-between">
                <p className="kind">Men</p>
                <ul className="stars d-flex">
                    <li className="star"><FontAwesomeIcon icon={faStar}  className="start"/></li>
                    <li className="star"><FontAwesomeIcon icon={faStar} className="start"/></li>
                    <li className="star"><FontAwesomeIcon icon={faStar}className="start" /></li>
                    <li className="star"><FontAwesomeIcon icon={faStar}className="start" /></li>
                    <li className="star"><FontAwesomeIcon icon={faStar} className="start"/></li>
                </ul>
            </div>
            <div className="Salery">
                    <p className="lead">150</p>
                    <h1 className="sale">EGP {e.price}</h1>
            </div>
            
            <div className="detils d-flex justify-content-between">
            <Link to={`/Woman/${e.id}`} type="button" className="btn view btn-info btn-sm button" >View</Link>
            <Link to={"/Cart"} type="button" className="btn btn-success">Add Cart</Link>
            
</div>

    </div>
    </div>
   </div>

   )

  

        
       
      
        
    
})
}

</div>
</div>
</div>
    </>)

    
    
  
}

export default SomeProduct;