import { Link } from 'react-router-dom';
import './Homepage/source/style.css'


function CartSideBar(){
    return(
        <div className="offcanvas offcanvas-end p-3" tabIndex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
        <div className="offcanvas-header">
          <h5 className="offcanvas-title" id="offcanvasRightLabel">Shopping cart</h5>
          <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div className="offcanvas-body">
          ss
        </div>
        <div className="offcanvas-footer">
        <div className='total'>
            <h6>Subtotal</h6>
            <p className='money'>0EGP</p>
        </div>
        <div className='Push-money'>
        <div className="d-grid gap-2">
  <Link to={"/Cart"} className="btn btn-View" type="button">View cart</Link>
  <Link to={"/Checkout"} className="btn btn-Cheked" type="button">Checkout</Link>
</div>
        </div>
        </div>
      </div>
    )
}
export default CartSideBar;