
import { useEffect, useState } from "react";
import { Link,useParams } from "react-router-dom";
import "../../Homepage/source/style.css"
import ClearIconDemot from "./dropDown";
import BasicDemo from "./Counter";
import DisabledDemo from "../../Review-Description";
import NavBar from "../../NaveBar";
import axios from "axios";
function ProductDetils(){

    let {productid} =useParams();
    const [product,setProduct]=useState({})
    useEffect(()=>{ 
    fetch(`https://fakestoreapi.com/products/${productid}`)
    .then((res)=>res.json())
    .then((dat)=>setProduct(dat)).then((window.scrollTo(0,0)))},[productid])

const cart =()=>{
  axios.post("http://localhost:9000/cart",
  {product})
  .then((data)=>{console.log(data)})
}

    
    // useEffect(() => {
    //   window.scrollTo(0, 0)
    // },[productid])
   
    return(
      
        <>
        
        <NavBar/>
<div className="productDetils">
  <div className="container-fluid">
    <div className="col-md-12">
    <div className="card mb-3">
  <div className="row g-0">
    <div className="col-md-5">
    <div className="img"> 
              <div className="productDetilsImageCard" style={{ backgroundImage: 'url(' + product.image + ')' }}></div> 
     </div>
    </div>
    <div className="col-md-7">
      <div className="card-body">
        <h5 className="card-title kind">{product.kind}</h5>
        <h5 className="card-title">{product.title}</h5>
        <ul className="color-list d-flex">
          {/* <li className="color blue" style={{background:"#00275A"}}></li>
          <li className="color yellow" style={{background:"#B79F49"}}></li>
          <li className="color green"  style={{background:"#159660"}}></li> */}
          {
          product.color?.map((colr,index)=>{
            return( 
            <li className="chooseColer" key={index}>
              <label htmlFor={`color_${index}`} style={{backgroundColor:`${colr}`,width:"50px",height:"50px",borderRadius:"50%" ,margin:"0 5px" ,cursor:"pointer"}}>
              </label>
              <input  id={`color_${index}`}  name="me" type="radio" className="cheked_coler"/>
            
            </li>)
          })}
        </ul>
        <div className="dropdown d-flex">
          <h1 className="size">Size</h1>
          <ClearIconDemot/>
        </div>

      
        <h1 className="card-text money"><small className="text-body-secondary throw">150</small>EGP{ product.price}<small className="text-body-secondary sale"> + Free Shipping</small></h1>
      
        <div className="number">
        <BasicDemo/>
        <Link onClick={()=>{cart()}} to={"/Cart"} type="button" className="btn btn-success add"><i className="fa-solid fa-cart-shopping"></i> Add Cart</Link>
       </div>
      </div>
    </div>
  </div>
</div>
    </div>
  </div>
</div>

<div className="Descriptio_Reviews">
  <div className="container-fluid">
<div className="row">
  <div className="col-md-12">
<DisabledDemo/>
</div>
</div>
</div>
</div>

</>

    )
}
export default ProductDetils;